/* ----------------------------------------
  IMAGES
---------------------------------------- */
.u-block-ratio {
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: var(--color-primary);
  background-color: var(--color-primary)
}
.u-block-ratio:after {
    content: "";
    display: block;
    height: 0;
  }
.u-block-ratio.u-block-ratio--16-9:after {
      padding-bottom: 56.25%;
    }
.u-block-ratio.u-block-ratio--100:after {
      padding-bottom: 100%;
    }
img {
  height: auto;
}
img,
svg {
  vertical-align: middle;
  line-height: 0;
  max-width: 100%;
}
/* ----------------------------------------
  BORDERS
---------------------------------------- */
.u-pill {
  border-radius: 9999px!important;
}
/* ----------------------------------------
  ROW
---------------------------------------- */
.row {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  max-width: 160rem;
  max-width: var(--max-width)
}
@media (min-width: 2000px) {
.row {
    max-width: 180rem;
    max-width: var(--max-width-large)
}
  }
/* ----------------------------------------
  LAZY
---------------------------------------- */
.lazy {
  opacity: 0;
  transition: opacity .3s ease
}
.lazy.is-loaded {
    opacity: 1;
  }
/* ----------------------------------------
  LABEL
---------------------------------------- */
.label {
  font-size: 1.2rem;
  color: hsla(0,0%,100%,.6);
  letter-spacing: 5px;
  margin-bottom: 1rem;
  display: block;
}
/* ----------------------------------------
  SITE OVERLAY
---------------------------------------- */
.site-overlay {
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 50;
  opacity: 0;
  visibility: hidden;
	transition: opacity 700ms, visibility 0s 700ms;
}
/* ----------------------------------------
  INLINE LINK
---------------------------------------- */
.inline-link {
  color: #7C7C7C;
  color: var(--color-grey-70);
  font-size: 1.4rem;
  line-height: 1;
  display: inline-block;
  transition: all .2s ease;
}
.is-desktop .inline-link:hover {
    color: #6695a4;
    color: var(--color-blue);
  }
/* ----------------------------------------
  INLINE LIST
---------------------------------------- */
.inline-list {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
  padding: 1rem 0
}
@media (min-width: 768px) {
.inline-list {
    overflow: visible
}
  }
.inline-list li {
    display: inline-block
  }
.inline-list li:not(:last-child) {
      margin-right: 3rem;
    }
.inline-list li a {
      display: inline-block;
      color: #A8A8A8;
      color: var(--color-grey-50);
      transition: all .3s ease;
      position: relative;
      padding-left: 1.8rem;
      font-size: 1.4rem
    }
@media (min-width: 768px) {
.inline-list li a {
        font-size: 1.6rem
    }
      }
.inline-list li a:before {
        content: "";
        position: absolute;
        top: .2rem;
        left: 0;
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        background-color: #D5D5D5;
        background-color: var(--color-grey-30);
        transition: all .3s ease
      }
@media (min-width: 768px) {
.inline-list li a:before {
          top: .4rem
      }
        }
.inline-list li a:hover {
        color: #6695a4;
        color: var(--color-blue);
      }
.inline-list li a.is-active {
        color: #6695a4;
        color: var(--color-blue)
      }
.inline-list li a.is-active:before {
          background-color: #6695a4;
          background-color: var(--color-blue);
        }
.inline-list li.is-active a, .inline-list li.current-cat a {
        color: #6695a4;
        color: var(--color-blue)
      }
.inline-list li.is-active a:before, .inline-list li.current-cat a:before {
          background-color: #6695a4;
          background-color: var(--color-blue);
        }
/* ----------------------------------------
